<template>
  <div id="product">
    <div id="main">
    </div>
  </div>
</template>

<script>
export default {
  name: 'product'
}
</script>

<style scoped>
  #main{
    background-size: cover;
    overflow: hidden;
    position: relative;
    background: #00CED1;
  }

  @media only screen and (max-device-width: 767px) {
    #main {
      width: 100%;
      height: 350px;
    }
  }

  @media only screen and (min-device-width: 768px) {
    #main {
      width: 100%;
      height: 640px;
    }
  }
</style>



